import React, { Component, Fragment } from 'react'
import gql from 'graphql-tag';
import {Query} from 'react-apollo';
import {Link} from 'react-router-dom';
import ClassNames from 'classnames';
import LaunchGallery from './LaunchGallery';


const LAUNCH_QUERY=gql`
  query LaunchQuery($flight_number:Int!){
    launch(flight_number: $flight_number){
      flight_number
      mission_name
      launch_year
      launch_success
      launch_date_local
      rocket{
        rocket_id
        rocket_name
        rocket_type
      }
      links{
        mission_patch_small
        flickr_images
      }
    }
  }
`;

export class Launch extends Component {
  render() {
    let {flight_number} = this.props.match.params;
    flight_number = parseInt(flight_number);
    return (
      <>
        <Query query={LAUNCH_QUERY} variables={{flight_number}}>
        {
          ({ loading, error, data })=>{
            if(loading) return (<div className="d-flex justify-content-center">
            <div className="spinner-border" 
            style={{width: '3rem', height: '3rem'}} role="status">
            <span className="sr-only">Loading...</span>
            </div>
        </div>)
            if(error) console.log(error)
            const {mission_name, flight_number, launch_year, launch_success, rocket:{
              rocket_id, rocket_name, rocket_type}, links:{mission_patch_small, flickr_images}} = data.launch;
              const rocketImages= {flickr_images}.flickr_images;
            return <div>
              <h1 className="display-4 my-3">Mission: <span className="text-dark">
              {mission_name} </span></h1>
              <div className="row">
                <div className="col-md-6">
                  <h4 className="my-3">Launch Details</h4>
                  <ul className="list-group">
                    <li className="list-group-item">
                    Flight Number: {flight_number}
                    </li>
                    <li className="list-group-item">
                    Launch Year: {launch_year}
                    </li>                  
                    <li className="list-group-item">
                    Launch Success: <span className={ClassNames({
                      'text-success': launch_success,
                      'text-danger': !launch_success
                    })}>{launch_success ? 'Yes':'No'}</span>
                    </li>
                  </ul>

                  <h4 className="my-3">Rocket Details</h4>
                  <ul className="list-group">               
                    <li className="list-group-item">
                      Rocket ID: {rocket_id}
                    </li>                    
                    <li className="list-group-item">
                      Rocket Name: {rocket_name}
                    </li>                    
                    <li className="list-group-item">
                      Rocket Type: {rocket_type}
                    </li>
                  </ul>
                </div>

                <div className="col-md-6 align-items-center d-flex">
                  <img src={mission_patch_small} alt="mission patch" className="mx-auto d-block"/>
                </div>
              </div>
              <LaunchGallery rocketImages={rocketImages}/>
              <hr/>
              <Link to="/" className="btn btn-secondary">Back</Link>
            </div>
          }
        }
        </Query>
      </>
    )
  }
}

export default Launch
