import React from 'react'

export default function LaunchGallery({rocketImages}) {
  function renderGallery(rocketImages){
    if(rocketImages === undefined || rocketImages.length == 0){
      return <p className="text-center">No Photos</p>
    } else{
      return <div className="d-flex flex-wrap">
      {
        rocketImages.map( function(flick){
          let bgUrl = 'url('+flick+')';
          return <div key={flick} style={{width:'330px', height:'400px', 
          background:bgUrl, backgroundSize:'cover', backgroundPosition:'center', 
          display:'inline-block', margin: '10px auto'}}/>
        }
        )
      }
    </div>
    }
  }
  return (
    <div className="row">
                <div className="col-md-12">
                  <h4 className="my-3">Gallery</h4>
                  {
                    renderGallery(rocketImages)
                  }
                </div>
              </div>
  )
}
